/* Global reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* App container */
.app-container {
  width: 100%;
  max-width: 1440px; /* prevents stretching too wide */
  margin: 0 auto;
  padding: 0 16px; /* space on sides for mobile */
  font-family: Arial, sans-serif;
}

/* Ensure images & videos scale */
img, video {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Responsive text */
h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  word-break: break-word;
}

p {
  line-height: 1.6;
}

/* Mobile first adjustments */
@media (max-width: 768px) {
  .app-container {
    padding: 0 12px;
  }
}

/* Tablet */
@media (max-width: 1024px) {
  .app-container {
    padding: 0 20px;
  }
}
